form {
  margin: 30px auto 10px;
  text-align: center;
}

.file_input {
  height: 0;
  width: 0;
  opacity: 0;
  padding: 0;
  margin: 0;
}

label {
  display: block;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  margin: auto;
  line-height: 30px;
  color: #4e4e4e;
  font-weight: bold;
  font-size: 24px;
  text-align: center;
  align-items: center;
}
label:hover {
  background: #4e4e4e;
  color: white;
}
.output {
  height: 60px;
  font-size: 0.8rem;
}
.error {
  color: #ff2424;
}

.img-grid{
  margin: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}
.img-wrap{
  overflow: hidden;
  position: relative;
  opacity: 0.6;
  padding: 50%;
}
.img-wrap img{
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}
.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  border: 3px solid white;
}